* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  background-image: url("https://www.transparenttextures.com/patterns/inspiration-geometry.png");
}

body ::selection {
  color: #fddb3a;
  background: #000;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  line-height: 1.2;
}

