.skills_container {
  margin: 100px 60px;

  @media screen and (max-width: 799px) {
    margin: 12px;
  }

  ul {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 799px) {
      display: grid;
      padding: 0;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 16px;
    }

    li {
      font-size: 42px;
      text-align: center;

      p {
        font-size: 13px;
        color: #262626;
        margin-top: 0;
      }
    }
  }
}
