.contact_container {
  text-align: center;

  h1 {
    padding: 50px 0 0;

    @media screen and (max-width: 799px) {
      font-size: 30px;
      padding: 50px 0 0;
    }
  }
}

.contact_item {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.social_media {
  margin: 30px;
  background-color: #262626;
  color: #fff;
  border-radius: 50%;

  &:hover {
    transition: 0.2s ease-in-out;
    color: #ffd523;
  }
}

.social_media_item {
  font-size: 28px;
  margin: 20px;

  @media screen and (max-width: 799px) {
    margin: 12px;
  }
}
